"use client";
import React, { useState, useEffect } from "react";
import { useParams } from "next/navigation";

// Replace this with your real API endpoint
const fetchClassroomById = async (id: string) => {
  // Example: fetch(`/api/classrooms/${id}`)
  // For demonstration, return a mock after a delay
  // Replace this with your real fetch logic
  return new Promise<{ id: string; name: string }>((resolve) =>
    setTimeout(() => resolve({ id, name: `Classroom ${id}` }), 300)
  );
};

const ClassroomName = ({ id }: { id: string }) => {
  const [classroom, setClassroom] = useState<{ id: string; name: string } | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setLoading(true);
    fetchClassroomById(id).then((data) => {
      setClassroom(data);
      setLoading(false);
    });
  }, [id]);

  if (loading) return <span className="text-gray-400">Loading...</span>;
  if (!classroom) return <span className="text-red-500">Classroom Not Found</span>;
  return <span className="text-xl font-semibold">{classroom.name}</span>;
};

const Page = () => {
  const [tab, setTab] = useState(0);
  const params = useParams();

  return (
    <section className="w-full rounded-2xl bg-white p-7">
      <div className="flex items-center justify-between mb-6">
        {/* Fetch and display classroom name by ID */}
        {typeof params.id === "string" && <ClassroomName id={params.id} />}
      </div>
      <div className="mt-4 pt-3">
        <div className="bg-[#fafbfc] rounded-2xl p-6">
          {/* Tabs */}
          <div className="flex gap-8 border-b border-gray-200 mb-6">
            <button
              className={`pb-2 font-semibold text-base focus:outline-none ${
                tab === 0
                  ? "border-b-2 border-black text-black"
                  : "text-gray-400 hover:text-black"
              }`}
              onClick={() => setTab(0)}
            >
              Item one
            </button>
            <button
              className={`pb-2 text-base focus:outline-none ${
                tab === 1
                  ? "border-b-2 border-black text-black font-semibold"
                  : "text-gray-400 hover:text-black"
              }`}
              onClick={() => setTab(1)}
            >
              Item two
            </button>
            <button
              className="pb-2 text-base text-gray-300 cursor-not-allowed"
              disabled
            >
              Item three
            </button>
          </div>
          {/* Content box */}
          <div className="bg-white rounded-xl border border-gray-200 p-5">
            {tab === 0 && <>Item one</>}
            {tab === 1 && <>Item two</>}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Page;